@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

body {
  font-family: 'Roboto', sans-serif;
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
}

.Instinct {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #14240b;
  padding: 20px;
  margin: 0 20px; /* Добавление отступов по краям экрана */
  min-height: 100vh; /* Установка минимальной высоты для Instinct */
  box-sizing: border-box; /* Учет отступов и границ в общей ширине и высоте */
  border-radius: 10px; /* Добавление закругленных углов */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Добавление тени */
}

.talents {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* Отображение в два столбика */
  gap: 20px;
  justify-content: center;
}

@media (max-width: 600px) {
  .talents {
    grid-template-columns: 1fr; /* Отображение в один столбик на маленьких экранах */
  }
}

.talent {
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s, box-shadow 0.2s;
}

.talent:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2); /* Увеличение тени при наведении */
}

.talent img {
  max-width: 100%;
  max-height: 250px; /* Увеличение высоты изображения */
  object-fit: cover; /* Корректное отображение изображения */
  border-radius: 10px;
}

.talent h2 {
  margin: 10px 0;
  font-size: 1.5em;
  color: #333;
}

.talent button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.talent button:hover {
  background-color: #0056b3;
  transform: scale(1.05); /* Увеличение кнопки при наведении */
}

.talent-info {
  text-align: center;
}

.talent-info img {
  max-width: 100%;
  max-height: 250px; /* Увеличение высоты изображения */
  object-fit: cover; /* Корректное отображение изображения */
  border-radius: 10px;
}

.talent-info h2 {
  margin: 10px 0;
  font-size: 1.5em;
  color: #333;
}

.talent-info p {
  font-size: 1.2em;
  color: #666;
}

.talent-info button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
}

.talent-info button:hover {
  background-color: #0056b3;
  transform: scale(1.05); /* Увеличение кнопки при наведении */
}

.menu-button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s, transform 0.2s;
  margin-top: 20px;
}

.menu-button:hover {
  background-color: #0056b3;
  transform: scale(1.05); /* Увеличение кнопки при наведении */
}

.Instinct h1 {
  font-size: 2em;
  color: #c5ec68;
  margin-bottom: 20px;
}
